import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-country-list',
  templateUrl: './country-list.page.html',
  styleUrls: ['./country-list.page.scss'],
})
export class CountryListPage {
  public type;
  public countries: any[];

  constructor(
    private data: DataService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {
    activatedRoute.params.subscribe((params) => {this.type = params['type']; this.getCountries(); });

  }

  getCountries() {
    if(this.type === 'n') {
      this.countries = this.data.getN();
    }
    if(this.type === 's') {
      this.countries = this.data.getS();
    }
    if(this.type === 'w') {
      this.countries = this.data.getW();
    }
    if(this.type === 'e') {
      this.countries = this.data.getE();
    }
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Inbox' : '';
  }

  goHome(){
    this.router.navigate(['/']);
  }

  goApt() {
    this.router.navigate(['/apteka']);
  }

  goCheck() {
    this.router.navigate(['/check']);
  }
}
